<template>
  <div>
    <div class="stat__header">
      <div class="fl">订单金额分布</div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner">
        <div id="dealChart" class="line-chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { tradingMoney } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'dealChart',
      optionsData: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: 'left',
          bottom: 0,
          top: 30,
          right: 0,
          containLabel: true
        },
        xAxis: {
          splitLine: {
            show: false
          },
          type: 'category',
          data: []
        },
        yAxis: {
          splitLine: {
            show: false
          },
          type: 'value'
        },
        series: {
          name: '订单金额分布',
          type: 'bar',
          barMaxWidth: 25,
          barCategoryGap: '10%',
          data: [320, 302, 301, 334, 390, 330, 320]
        }
      }
    }
  },
  methods: {

    getData() {
      this.loading = true
      tradingMoney(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.optionsData.xAxis.data = res.data.xAxis
            this.optionsData.series.data = res.data.yAxis // 默认会员
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 240px;
}
</style>



