<template>
  <div>
    <div class="stat__header">
      <div class="fl">下单用户构成</div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner">      
        <div id="activeChart" class="line-chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { tradingOrderPeople } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'activeChart',
      optionsData: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: 'left',
          containLabel: true
        },
        xAxis: {
          type: 'value'
        },
        yAxis: {
          splitLine: {
            show: false
          },
          type: 'category',
          data: ['新老客户占比']
        },
        series: [
          {
            name: '新客户',
            type: 'bar',
            stack: '总量',
            barMaxWidth: 50,
            label: {
              normal: {
                show: true,
                position: 'insideRight',
                formatter: function(data) {
                  if (data.value === 0) {
                    return ''
                  } else {
                    return data.value
                  }
                }
              }
            },
            data: []
          },
          {
            name: '老客户',
            type: 'bar',
            stack: '总量',
            barMaxWidth: 50,
            label: {
              normal: {
                show: true,
                position: 'insideRight',
                formatter: function(data) {
                  if (data.value === 0) {
                    return ''
                  } else {
                    return data.value
                  }
                }
              }
            },
            data: []
          }
        ]
      }

    }
  },
  methods: {
    getData() {
      this.loading = true
      tradingOrderPeople(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.optionsData.series[0].data = res.data.series[0].data
            this.optionsData.series[1].data = res.data.series[1].data

            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 240px;
}
</style>



