<template>
<div class="stat__layout" >
 <timer @change="getTime"></timer>
 <general class="mt-20" :time-data="value" ></general>
 <el-row :gutter="20" >
 <el-col :xs="24" :sm="24" :md="12" :lg="14" :xl="14" class="mt-20">
    <member  :time-data="value"></member>
 </el-col>
  <el-col :xs="24" :sm="24" :md="12" :lg="10" :xl="10" class="mt-20">
   <trading-source  :time-data="value"></trading-source>
 </el-col>
 </el-row>
 <trading-money class="mt-20" :time-data="value"></trading-money>
  <trading-area class="mt-20" :time-data="value" ></trading-area>
</div>
</template>
<script>
import timer from './../components/time'
import general from './general'
import member from './member'
import tradingSource from './source'
import tradingMoney from './sale'
import tradingArea from './area'
export default {
  components: {
    member,
    tradingSource,
    general,
    tradingMoney,
    tradingArea,
    timer
  },
  data() {
    return {
      value: ['', '']
    }
  },
  methods: {
    getTime(time) {
      this.value = time
    }
  }
}
</script>

<style>
</style>



