<template>
  <div >
    <div class="stat__header">
      <div class="fl">交易概况</div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner">
        <ul class="trading-items">
          <li class="trading-item color1">
            <ul class="trading-previews">
              <li>
                <p>浏览量(pv)</p>
                <count-to :endVal="data.pv" class="count-val"></count-to>
              </li>
              <li>
                <p>访客数(uv)</p>
                <count-to :endVal="data.uv" class="count-val"></count-to>
              </li>
            </ul>
            <div class="trapezoid1">
              <span>访客</span>
            </div>
          </li>
          <li class="trading-item color2">
            <ul class="trading-previews">
              <li>
                <p>下单人数</p>
                <count-to :endVal="data.ordersPeopleNum" class="count-val"></count-to>
              </li>
              <li>
                <p>下单笔数</p>
                <count-to :endVal="data.ordersNum" class="count-val"></count-to>
              </li>
              <li>
                <p>下单金额</p>
                <count-to :endVal="data.ordersMoneyNum" :duration="1500"  :decimals="2" class="count-val"></count-to>
              </li>
            </ul>
            <div class="trapezoid2">
              <span>下单</span>
            </div>
          </li>
          <li class="trading-item color3">
            <ul class="trading-previews">
              <li>
                <p>付款人数</p>
                <count-to :endVal="data.payOrdersPeopleNum" class="count-val"></count-to>
              </li>
              <li>
                <p>付款订单数</p>
                <count-to :endVal="data.payOrdersNum" class="count-val"></count-to>
              </li>
              <li>
                <p>付款金额</p>
                <count-to :endVal="data.payOrdersMoneyNum" :duration="1500"  :decimals="2" class="count-val"></count-to>
              </li>
              <li>
                <p>付款件数</p>
                <count-to :endVal="data.payOrdersProductNum" class="count-val"></count-to>
              </li>
              <li>
                <p>客单价</p>
                <count-to :endVal="data.customerMoneyNum" :duration="1500" :decimals="2"  class="count-val"></count-to>
              </li>
            </ul>
            <div class="trapezoid3">
              <span>付款</span>
            </div>
          </li>
          <div class="trading-line">
             <span class="span-1">
              下单转化率{{data.ordersPrecent}}%
            </span>
            <span  class="span-2">
                成交转化率{{data.ordersFinishPrecent}}%
            </span>
            <span class="span-3">
                付款转化率{{data.payOrdersPrecent}}%
            </span>
          </div>
        </ul>
        <div id="tradingChart" class="line-chart">
           

        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { tradingSummarize } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'tradingChart',
      optionsData: {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: [
            '付款金额',
            '付款人数',
            '付款件数',
            '下单转化(%)',
            '付款转化(%)',
            '成交转化率(%)'
          ],
          left: 'center',
          top: 0
        },
        grid: {
          top: 40,
          left: 0,
          right: 0,
          bottom: 0,
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: true,
          splitLine: {
            show: false
          },
          data: ['00-00', '0-0', '0-0']
        },
        yAxis: [
          {
            axisLine: {
              show: false
            },
            splitLine: {
              show: false,
              lineStyle: {
                type: 'dotted'
              }
            },
            type: 'value'
          },
          {
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                type: 'dotted'
              }
            },
            type: 'value',
            axisLabel: {
              formatter: value => {
                var texts = []
                const obj = value
                texts.push(obj + '%')
                return texts
              }
            }
          }
        ],
        series: [
          {
            name: '付款金额',
            type: 'line',
            smooth: true,
            data: []
          },
          {
            name: '付款人数',
            type: 'line',
            smooth: true,
            data: []
          },
          {
            name: '付款件数',
            type: 'line',
            smooth: true,
            data: []
          },
          {
            name: '下单转化(%)',
            type: 'line',
            smooth: true,
            data: [],
            yAxisIndex: 1
          },
          {
            name: '付款转化(%)',
            type: 'line',
            smooth: true,
            yAxisIndex: 1,
            data: []
          },
          {
            name: '成交转化率(%)',
            type: 'line',
            smooth: true,
            yAxisIndex: 1,
            data: []
          }
        ]
      },
      data: {
        'customerMoneyNum': 0,
        'ordersFinishPrecent': 0,
        'ordersMoneyNum': 0,
        'ordersNum': 0,
        'ordersPeopleNum': 0,
        'ordersPrecent': 0,
        'payOrdersMoneyNum': 0,
        'payOrdersNum': 0,
        'payOrdersPeopleNum': 0,
        'payOrdersPrecent': 0,
        'payOrdersProductNum': 0,
        'pv': 0,
        'uv': 0
      }
    }
  },
  methods: {
    getData() {
      this.loading = true
      tradingSummarize(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.data = res.data.exchangeSummarize
            this.optionsData.xAxis.data = res.data.date
            this.optionsData.series[0].data = res.data.payMoney.data
            this.optionsData.series[1].data = res.data.payPeople.data
            this.optionsData.series[2].data = res.data.payNumber.data
            this.optionsData.series[3].data = res.data.payOrderPrecent.data
            this.optionsData.series[4].data = res.data.payPrecent.data
            this.optionsData.series[5].data = res.data.payFinishPrecent.data
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  },
  created() {

  }
}
</script>
<style lang="scss" scoped>
.trading-items{
  position: relative;
  width: 88%;
}
.trading-line{
  position: absolute;
  width: 211px;
  height: 140px;
  right: -164px;
  top: 25px;
  background:url('./../../../../assets/images/line-bg.png');
  font-size: 12px;
  color: #666;
  .span-1{
    position: absolute;
    width: 60px;
    left: 118px;
    top: 28px;   
  }
  .span-2{
    position: absolute;
    width: 60px;
    left: 190px;
    top: 55px;   
  }
    .span-3{
    position: absolute;
    width: 60px;
    left: 98px;
    top: 85px;   
  }
}
.line-chart {
  width: 100%;
  height: 260px;
  margin-top:40px;
}
.trading-previews {
  float: left;
  padding: 13px 30px;
  li {
    float: left;
    font-size: 12px;
    color: #666;
    width: 110px;
    text-align: left;
  }
  .count-val {
    font-size: 12px;
    margin-top: 5px;
  }
}
.trading-item {
  width: 100%;
  height: 60px;
  overflow: hidden;
  margin-bottom: 2px;
}
.color {
  &1 {
    background: #ddeff9;
  }
  &2 {
    background: #e5fafa;
    width: calc(100% - 20px);
  }
  &3 {
    background: #fff9e4;
    width: calc(100% - 40px);
  }
}
.trapezoid1 {
  position: relative;
  float: right;
  width: 160px;
  height: 0px;
  border-top: 60px solid #37a2da;
  border-left: 20px solid #ddeff9;
  border-right: 20px solid #fff;
  color: #fff;
  font-size: 12px;
  span {
    position: absolute;
    top: -42px;
    left: 46px;
  }
}
.trapezoid2 {
  color: #fff;
  font-size: 12px;
  position: relative;
  float: right;
  width: 120px;
  height: 0px;
  border-top: 60px solid #67e0e3;
  border-left: 20px solid #e5fafa;
  border-right: 20px solid #fff;
  span {
    position: absolute;
    top: -42px;
    left: 25px;
  }
}
.trapezoid3 {
  color: #fff;
  font-size: 12px;
  position: relative;
  float: right;
  width: 80px;
  height: 0px;
  border-top: 60px solid #ffdb5c;
  border-left: 20px solid #fff9e4;
  border-right: 20px solid #fff;
  span {
    position: absolute;
    top: -42px;
    left: 7px;
  }
}
</style>



