<template>
  <div>
    <div class="stat__header">
      <div class="fl">下单来源地域分布</div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :xs="24" :sm="24" :md="24" :lg="10" :xl="10" class="stat__inner">
        <div id="areaChart" class="line-chart"></div>
      </el-col>
      <el-col :xs="24" :sm="24" :md="24" :lg="14" :xl="14" class="border-left">
        <div class="table-box">
          <table class="stat-inner__table mt-20">
            <tr>
              <th style="width:10%">序号</th>
              <th style="width:10%">地区</th>
              <th style="width:20%">成交金额</th>
              <th style="width:20%">成交人数</th>
              <th style="width:20%">访客数</th>
              <th style="width:20%">访客成交转化率</th>
            </tr>
          </table>
          <div class="table-box__inner">
            <table class="stat-inner__table">
              <tr v-for="(item,index) in tableData" :key="index">
                <td style="width:10%">{{index+1}}</td>
                <td style="width:10%">{{item.areaName}}</td>
                <td style="width:20%">{{$cms.float(item.areaDealMoney)}}</td>
                <td style="width:20%">{{item.areaDealPeopleNum}}</td>
                <td style="width:20%">{{item.areaVisitPeopleNum}}</td>
                <td style="width:20%">{{item.areaDealPrecent}}%</td>
              </tr>
            </table>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import echarts from 'echarts'
const geoJson = require('./../../../../../static/lib/china.json')
import { tradingArea } from '@/api/statistics/index.js'

export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'areaChart',
      optionsData: {
        tooltip: {
          trigger: 'item',
          // formatter: '{b}<br/>{c}人'
          formatter: function(params) {
            var res = params.name + '<br/>'
            var data = params.data
            if (params.data) {
              res += '成交人数：' + data.value[0] + '</br>' + '访客数：' + data.value[1]
            } else {
              res += '成交人数：0</br>访客数：0'
            }
            return res
          }
        },
        visualMap: {
          realtime: false,
          calculable: true,
          left: 20,
          inRange: {
            color: ['lightskyblue', 'yellow', 'orangered']
          }
        },
        series: [
          {
            name: '店铺区域分布',
            type: 'map',
            mapType: 'china', // 自定义扩展图表类型
            itemStyle: {
              normal: { label: { show: true }},
              emphasis: { label: { show: true }}
            },
            data: []
          }
        ]
      },
      tableData: []
    }
  },
  methods: {
    getData() {
      this.loading = true
      tradingArea(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            var arr = []
            res.data.area.forEach(v => {
              var obj = {
                name: v.areaName,
                value: [v.areaDealPeopleNum, v.areaVisitPeopleNum]
              }
              arr.push(obj)
            })
            this.optionsData.series[0].data = arr
            this.tableData = res.data.area
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    initChart() {
      // 初始化图表
      this.$nextTick(() => {
        if (this.el && this.el !== '') {
          echarts.registerMap('china', geoJson)
          this.chart = echarts.init(document.getElementById(this.el), 'walden')
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 450px;
}
.stat-inner__table{
  text-align: center;
}
.table-box {
  height: 470px;
  width: 100%;
  padding: 0 20px;
  &__inner {
    height: 400px;
    overflow: auto;
    width: 100%;
  }
}
</style>



