<template>
  <div>
    <div class="stat__header">
      <div class="fl">下单渠道分布</div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner">
        <div id="pieChart" class="line-chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { tradingChannel } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'pieChart',
      optionsData: {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'right',
          data: []
        },
        series: [
          {
            name: '下单渠道',
            type: 'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '18',
                  fontWeight: 'bold'
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: []
          }
        ]
      }
    }
  },
  methods: {
    getData() {
      this.loading = true
      tradingChannel(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.optionsData.series[0].data = res.data
            this.optionsData.legend.data = res.data.map(v => v.name)
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 240px;
}
</style>



